angular
  .module('app', ['ui.router']);

function Save() {
  var Save = this;
  Save.save = function (toBeSaved) {
    localStorage.setItem('mon_espace_de_stockage', JSON.stringify(toBeSaved));
  };

  Save.importSaved = function () {
    return JSON.parse(localStorage.getItem('mon_espace_de_stockage'));
  };
}

angular
  .module('app')
  .service('Save', Save);


angular
  .module('app')
  .component('poster', {
    templateUrl: 'app/component/poster.html',
    controller: function posterController() {
    },
    bindings: {
      movie: '<'
    }
  });


function movieController() {
  this.text = 'My brand new component!';
}

angular
  .module('app')
  .component('movie', {
    templateUrl: 'app/component/movie.html',
    controller: movieController
  });


angular
  .module('app')
  .component('list', {
    templateUrl: 'app/component/list.html',
    controller: function (Save, $http) {
      var $ctrl = this;
      $http.get('http://amc.ig.he-arc.ch:3003/movie/popular?language=fr')
      .then(function (result) {
        $ctrl.movies = result.data.results;
      });

      $ctrl.maListe = [];
      $ctrl.maListe = Save.importSaved();
      $ctrl.maVariable = 'Film Ã  voir';
      $ctrl.ajouterAListe = function () {
        $ctrl.maListe.push($ctrl.maVariable);
        Save.save($ctrl.maListe);
      };
      $ctrl.supprimerdeListe = function (el) {
        $ctrl.maListe.splice($ctrl.maListe.indexOf(el), 1);
        Save.save($ctrl.maListe);
      };
    }
  });

angular
  .module('app')
  .component('app', {
    templateUrl: 'app/home.html'
  });

